import Axios from 'axios';
import React, { useEffect } from 'react';
import './CurrencyBuilder.scss';
import axios from 'axios';
let bnm = require('bnm-api');

const CurrencyBuilder = () => {
   // bnm.exchangeRate('USD') 
   useEffect(() => {
      axios.get('https://api.bnm.gov.my/public/welcome', 
      { 'header ' : { 
         'Accept' : "application/vnd.BNM.API.v1+json",
         'Access-Control-Allow-Origin': '*'
       }})
      .then( res => {
         console.log(res);
      }).catch(err => {
         console.log(err);
      })
      
   }, []);
   bnm.exchangeRate('USD').then(response => console.log(response));

   // console.log(bnm);
   return (
      <div>
      </div>
   );
};

export default CurrencyBuilder;


